
@charset "UTF-8";
html {
  font-family: "Microsoft YaHei",'\5B8B\4F53',Arial,Helvetica,sans-serif;
  font-size: 12px;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, hgroup, menu, nav, section, summary, time, mark {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  font-weight: normal;
}
ul, ol {
  list-style: none;
}
a {
  text-decoration: none;
  background-color: transparent;
  -webkit-touch-callout: none;
}
img {
  border: 0;
  -webkit-touch-callout: none;
  vertical-align: top;
}
button, input, optgroup, select, textarea {
  margin: 0;
  padding: 0;
  line-height: normal;
  font: inherit;
  color: inherit;
  outline: none;
}
button {
  overflow: visible;
}
button, select {
  text-transform: none;
}
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
input[disabled] {
  cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
input {
  line-height: normal;
  padding: 0;
}
input[type="checkbox"],
input[type="radio"] {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0;
}
textarea {
  overflow: auto;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td, th {
  padding: 0;
}
@media screen and (max-width: 1242px) {
